import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Operacao_notificacao } from '../../../modelo/Operacao_notificacao'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'operacao_notificacao-dados-edit',   templateUrl: 'operacao_notificacao_dadosEdit.component.html' }) export class Operacao_notificacaoDadosEditComponent implements OnInit {    operacao_notificacao: Operacao_notificacao;    private sub: Subscription;    private errorMessage: string;     constructor(private router: Router,               private route: ActivatedRoute,               private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.operacao_notificacao.Operacao_notificacao";                 let id = +params['id'];         this.operacao_notificacao = new Operacao_notificacao();         this.operacao_notificacao.cd_operacao_notificacao = id;         o.objetoJson = this.operacao_notificacao;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a operacao_notificacao. ';       }      this.operacao_notificacao = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   }     onSubmit() {         }     onCancel(event: Event) {     event.preventDefault();     this.router.navigate(['/']);   }  }


